import React from "react";

export var closePopup;
export var openPopup;
export class Popup extends React.Component {
    constructor(props) {
        super(props)
        this.state = {visible: false, popup: null}
        closePopup = () => this.setState({visible: false})
        openPopup = (popup) => this.setState({popup: popup, visible: true})
    }
    render() {
        if (!this.state.visible) {
            return <></>;
        }
        return (
            <div className="popup-background-div">
        <div className="popup-clickable-background-div" onClick={closePopup}> 
        </div>
        {this.state.popup}
        </div>
        )
    }
}

