class userData extends React.Component {
  constructor(props) {
    super(props)
    this.state = {thing: "", thing2: ""}
    this.updateTest()
    getTurnedInMasterAssignments()
  }
  updateTest = async () => {
await axios.get('https://cambio-api.herokuapp.com/api/Students/', {
        headers: {
        'Authorization': `token ${localStorage.getItem("token")}`
        }
        })
        .then((response) => {
        console.log(response.data)
        this.setState({thing: JSON.stringify(response.data)})
        })
        .catch((error) => {
        console.error(error)
        })
        await axios.get('https://cambio-api.herokuapp.com/api/School/', {
            headers: {
            'Authorization': `token ${localStorage.getItem("token")}`
            }
            }).then(response => {
              this.setState({thing2: JSON.stringify(response.data)})
            })

  }

  render() {
    const queryClient = new QueryClient()
    return (<>
    <QueryClientProvider client={queryClient}>
       <Example />
     </QueryClientProvider>
    <div>{(localStorage.getItem("user"))} {this.state.thing}</div>
    <div>{this.state.thing2}</div>
    </>)
  }
}
function Example() {
  const { data } = useQuery('repoData', () =>
    fetch(`https://cambio-api.herokuapp.com/api/Groups/${localStorage.getItem("group")}/`).then(res =>
      res.json()
    )
  )



  return (
    <div>
      {JSON.stringify(data)}
    </div>
  )
}
