class App extends React.Component {
  constructor(props) {
    super(props)
    this.state = {updateKey: false, students: [], assignments: [], student: null, assignment: null, checkpoint: null}
    this.updateStudents()
    updateStudentAssignmentAppJS1 = (newState) => {
      this.setState(newState)
    }
    updatePage1 = () => {
      this.setState({updateKey: !this.state.updateKey})
    }
    
  }
  updateStudents = async () => {
    var students = await getClassInfo()
    var assignments = await getAssignmentNames()
    this.setState({students: students, assignments: assignments})
  }
  render() {
  getRequestToDoList()
  return (
    <div key={this.state.updateKey}>
    <HashRouter basename={process.env.PUBLIC_URL}>
      <Switch>
      <LoginRoute path="/login" exact component={LoginPage} />
      <LoginRoute path="/signup" exact component={SignupPage} />
      <LoginRoute path="/guest" exact component={GuestLogin} />
      <PrivateRoute path = "/class" exact component={ClassPage} />
      <Route path="/logout" exact component={LogoutPage} />
      <PrivateRoute path="/" exact component={HomePage} />
      <PrivateRoute path="/userData" exact component={userData} />
      <PrivateRoute path="/workspace" exact component={WorkspacePage} />
      <PrivateRoute path="/journey" exact component={JourneyPage} />
      <PrivateRoute path="/perksandrewards" exact component={PerksAndRewardsPage} />
      <PrivateRoute path="/portfolio" exact component={PortfolioPage} />
      <PrivateRoute path="/teacher" exact component={TeacherPage} />
      <PrivateRoute path="/teacher/class" exact component={TeacherClassPage} />
      <PrivateRoute path="/teacher/class/students" exact component={TeacherOverviewStudentPage} />
      <PrivateRoute path="/teacher/class/assignments" exact component={TeacherOverviewAssignmentPage} />
      <PrivateRoute path={`/teacher/class/assignment`} exact component={() => <TeacherOverviewStudentPage link={`/teacher/class/assignment/`}/>}/>
      <PrivateRoute path={`/teacher/class/student`} exact component={() => <TeacherSingleStudentOverviewPage studentName={this.state.student} />} />
      <PrivateRoute path={`/teacher/class/assignment/student`} 
      exact component={() => <TeacherSingleAssignmentPage assignmentName={this.state.assignment} studentName={this.state.student} checkpoint={this.props.checkpoint}/>}  /> 
      </Switch>
    </HashRouter>
    
      <Popup />
    </div>
  );
}}

export default App;
