import React, { } from 'react';
import { ChonkyIconFA } from 'chonky-icon-fontawesome';
import { integratePortfolioFolders, uploadFile1, createPortfolioFolder, deletePortfolioFolderOrFile } from "../backend.js";
import {
    ChonkyActions,
    ChonkyFileActionData,
    FileData,
    FileHelper,
    FullFileBrowser,
    ChonkyIconProps
} from 'chonky';

function makeid(len:any) {
    var result           = '';
    var characters       = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    var charactersLength = characters.length;
    for ( var i = 0; i < len; i++ ) {
      result += characters.charAt(Math.floor(Math.random() * 
 charactersLength));
   }
   return result;
}

var currentFolder:any;
//import { Position, Toaster, Intent } from "@blueprintjs/core";
//import { ChonkyIconFA } from 'chonky-icon-fontawesome';


interface CustomFileData extends FileData {
    parentId?: string;
    childrenIds?: string[];
}
interface CustomFileMap {
    [fileId: string]: CustomFileData;
}

//setChonkyDefaults({ iconComponent: ChonkyIconFA });

var selectedFile: any;

class CustomChonky extends React.Component<any, any> {
    constructor(props:any)
    {
      super(props)
      
      this.state = {

        files:[null],
        idCounter :0,
        fileMap:[null],
        currentFolderId:"",

        baseFileMap:[null],
        rootFolderId:"",

        currentFolderIdRef:null,
        folderChain : []
      };
   
    }

    componentDidMount(){

        // Init
        this.prepareCustomFileMap();

    }



    setCurrentFolderId = (folderId:string) => {

        this.setState({currentFolderId : folderId, currentFolderIdRef : folderId});

        // Update files chain
        this.useFiles(this.state.fileMap, folderId);

    }

    prepareCustomFileMap = async () => {
        
        // Sample Data
        /*var DemoFsMap = 
        {"rootFolderId":"qwerty123456","fileMap":{"qwerty123456":{"id":"qwerty123456","name":"Previous_Projects","isDir":true,"childrenIds":["Factures Clients", "Factures Fournisseurs", "Devis"],"childrenCount":3},
            "Factures Clients":{"id":"Factures Clients","name":"Factures Clients","isDir":true,"modDate":"2020-10-24T17:48:39.866Z","childrenIds":[],"childrenCount":0,"parentId":"qwerty123456"},
            "Factures Fournisseurs":{"id":"Factures Fournisseurs","name":"Factures Fournisseurs","isDir":true,"modDate":"2020-10-24T17:48:39.866Z","childrenIds":[],"childrenCount":0,"parentId":"qwerty123456"},
            "Devis":{"id":"Devis","name":"Devis","isDir":true,"modDate":"2020-10-24T17:48:39.866Z","childrenIds":[],"childrenCount":0,"parentId":"qwerty123456"}
        }} */
        // Simulate API CALL Timeout
        
        
        setTimeout(async () => {
            var DemoFsMap = 
        await integratePortfolioFolders({"rootFolderId":"qwerty123456",
        "fileMap":{
            "qwerty123456":{"id":"qwerty123456","name":"Startup_Assets","isDir":true,"childrenIds":[],"childrenCount":0}}}, this.props.type)
            console.log(DemoFsMap)
            const baseFileMap = (DemoFsMap.fileMap as unknown) as CustomFileMap;
            const rootFolderId = DemoFsMap.rootFolderId;
            
            console.log(baseFileMap);

            this.setState({ baseFileMap : baseFileMap, fileMap : baseFileMap, rootFolderId : rootFolderId, currentFolderIdRef:rootFolderId });

            // USE FILES
            this.useFiles(baseFileMap, rootFolderId);
            this.setCurrentFolderId(rootFolderId);

        }, 1000);
                
            
              
           
    };

    useFiles = (fileMap: CustomFileMap, currentFolderId: string) => {
 
        const currentFolder = fileMap[currentFolderId];
        const childrenIds = currentFolder.childrenIds!;
        const files = childrenIds.map((fileId: string) => fileMap[fileId]);
        // console.log(files);
        this.setState({files : files})

        this.useFolderChain(fileMap, currentFolderId);

    }

    useFolderChain = (
        fileMap: CustomFileMap,
        currentFolderId: string
    ) => {
        
        const currentFolder = fileMap[currentFolderId];

        const folderChain = [currentFolder];

        let parentId = currentFolder.parentId;
        while (parentId) {
            const parentFile = fileMap[parentId];
            if (parentFile) {
                folderChain.unshift(parentFile);
                parentId = parentFile.parentId;
            } else {
                break;
            }
        }

        this.setState({folderChain : folderChain});
    }



    resetFileMap = () => {
        this.setState({fileMap : this.state.baseFileMap })
        this.setState({currentFolderId :  this.state.rootFolderId })
    }

    deleteFiles = (files: CustomFileData[]) => {
         
            // Create a copy of the file map to make sure we don't mutate it.
            const newFileMap = { ...this.state.fileMap };

            files.forEach((file) => {
                // Delete file from the file map.
                delete newFileMap[file.id];

                // Update the parent folder to make sure it doesn't try to load the
                // file we just deleted.
                if (file.parentId) {
                    const parent = newFileMap[file.parentId]!;
                    const newChildrenIds = parent.childrenIds!.filter(
                        (id: any) => id !== file.id
                    );
                    newFileMap[file.parentId] = {
                        ...parent,
                        childrenIds: newChildrenIds,
                        childrenCount: newChildrenIds.length,
                    };
                }
            });
 
            this.setState({fileMap : newFileMap })
            this.useFiles(this.state.fileMap, this.state.currentFolderIdRef);
            
    };

    moveFiles = (
        files: CustomFileData[],
        source: CustomFileData,
        destination: CustomFileData) => {

            const newFileMap = { ...this.state.fileMap };
            const moveFileIds = new Set(files.map((f) => f.id));

            // Delete files from their source folder.
            const newSourceChildrenIds = source.childrenIds!.filter(
                (id) => !moveFileIds.has(id)
            );
            newFileMap[source.id] = {
                ...source,
                childrenIds: newSourceChildrenIds,
                childrenCount: newSourceChildrenIds.length,
            };

            // Add the files to their destination folder.
            const newDestinationChildrenIds = [
                ...destination.childrenIds!,
                ...files.map((f) => f.id),
            ];
            newFileMap[destination.id] = {
                ...destination,
                childrenIds: newDestinationChildrenIds,
                childrenCount: newDestinationChildrenIds.length,
            };

            // Finally, update the parent folder ID on the files from source folder
            // ID to the destination folder ID.
            files.forEach((file) => {
                newFileMap[file.id] = {
                    ...file,
                    parentId: destination.id,
                };
            });

        this.setState({fileMap : newFileMap })
        this.useFiles(this.state.fileMap, this.state.currentFolderIdRef);
        

    }

    createFolder = async (folderName: string) => {

        const newFileMap = { ...this.state.fileMap };
        // Create the new folder
        const newFolderId = makeid(15);

        newFileMap[newFolderId] = {
            id: newFolderId,
            name: folderName,
            isDir: true,
            modDate: new Date(),
            parentId: this.state.currentFolderIdRef,
            childrenIds: [],
            childrenCount: 0,
        };
        await createPortfolioFolder(folderName, newFolderId,this.state.currentFolderIdRef)
        await this.prepareCustomFileMap();

        // Update parent folder to reference the new folder.
        const parent = newFileMap[this.state.currentFolderIdRef];
        newFileMap[this.state.currentFolderIdRef] = {
            ...parent,
            childrenIds: [...parent.childrenIds!, newFolderId],
        };

        this.setState({fileMap : newFileMap })
        this.useFiles(this.state.fileMap, this.state.currentFolderIdRef);
        
        

    }


    uploadFile = () => {
            
        var input = document.createElement('input');
        input.type = 'file';
        input.name = 'file';
        var fileId = makeid(16);
        currentFolder = this.state.currentFolderIdRef;
        input.addEventListener("change", handleFiles1)
        function handleFiles1(this:any) {
            //this is where i fetch the file everything else is evil nonsense
            console.log(this.files)
            selectedFile = this.files[0]
            console.log("Why it not work!")
            console.log(JSON.stringify(this.files))
            //uploadFile(this.files)
            uploadFile1(this.files[0], this.files[0].name, fileId,currentFolder)
            //var newFiles = this.state.files.map(item => item);
            //newFiles.push(this.files[0]);
            //this.setState({files: newFiles})
        }    
        input.onchange = (e) => { 
            //@ts-ignore
            //var file = e.target.files[0]; 
            var newState = this.state.files.map((item:any) => item)
            newState.push({id: fileId, name: selectedFile.name,modDate: new Date(),
                parentId: this.state.currentFolderIdRef, "django_id": fileId})
            this.setState({files: newState})

            }

        input.click();


    }



    render() {
       
    
        const fileActions = [
            ChonkyActions.CreateFolder,
            ChonkyActions.UploadFiles,
            ChonkyActions.DownloadFiles,
            ChonkyActions.DeleteFiles
        ];
    
        
        const fileActionHandler = (
            setCurrentFolderId: (folderId: string) => void,
            deleteFiles: (files: CustomFileData[]) => void,
            moveFiles: (files: FileData[], source: FileData, destination: FileData) => void,
            createFolder: (folderName: string) => void,
            uploadFile: (fileName: string) => void
        ) => {
                return (data: ChonkyFileActionData) => {
                    if (data.id === ChonkyActions.OpenFiles.id) {
                        const { targetFile, files } = data.payload;
                        const fileToOpen = targetFile ?? files[0];
                        if (fileToOpen && FileHelper.isDirectory(fileToOpen)) {
                            console.log(fileToOpen.id);
                            setCurrentFolderId(fileToOpen.id);
                            return;
                        }
                    } else if (data.id === ChonkyActions.DeleteFiles.id) {
                        deletePortfolioFolderOrFile(data.state.selectedFiles[0])
                        deleteFiles(data.state.selectedFilesForAction!);
                    } else if (data.id === ChonkyActions.MoveFiles.id) {
                        moveFiles(
                            data.payload.files,
                            data.payload.source!,
                            data.payload.destination
                        );
                    }
                    else if (data.id === ChonkyActions.UploadFiles.id) {
                        uploadFile("filename");
                    }
                    else if (data.id === ChonkyActions.CreateFolder.id) {
                        const folderName = prompt('Provide the name for your new folder:');
                        if (folderName) createFolder(folderName);
                    }
                    else if (data.id === ChonkyActions.DownloadFiles.id) {
                        const downloadingFile = data.state.selectedFiles[0]
                        console.log(downloadingFile)
                        window.location.replace(downloadingFile.download)
                      }
                }
        };
 
        
        const handleFileAction = fileActionHandler(
            this.setCurrentFolderId,
            this.deleteFiles,
            this.moveFiles,
            this.createFolder,
            this.uploadFile,
        ); 
 
        const thumbnailGenerator = (file: FileData) =>
                file.thumbnailUrl ? `https://chonky.io${file.thumbnailUrl}` : null
         
        var iconFunction: React.FC<ChonkyIconProps> = React.memo((props) => {
            if (props.icon === "file"){
            return <div style={{padding: "20px", backgroundColor: "rgba(157, 230, 211, 1)", borderRadius: "15px", opacity: "0.5"}}><svg xmlns="http://www.w3.org/2000/svg" height="44px" viewBox="0 0 24 24" width="44px" fill="#000000"><path d="M0 0h24v24H0z" fill="none"/><path d="M14 2H6c-1.1 0-1.99.9-1.99 2L4 20c0 1.1.89 2 1.99 2H18c1.1 0 2-.9 2-2V8l-6-6zm2 16H8v-2h8v2zm0-4H8v-2h8v2zm-3-5V3.5L18.5 9H13z"/></svg></div>}
            return <ChonkyIconFA {...props} />
        })
        return (
            <>
            <FullFileBrowser 
                files={this.state.files} 
                folderChain={this.state.folderChain}
                onFileAction={handleFileAction}
                //fileActions={fileActions}
                thumbnailGenerator={thumbnailGenerator}
                iconComponent={iconFunction}
                disableDragAndDropProvider={this.props.disableDragAndDropProvider}
                 />
            </>
                 

        );

    }


};

export default CustomChonky;