import React from 'react';
import "../style.css"
import {PointTrackers} from "../reused-components/pointTrackers.js";
import { SideNav, Navbar } from "../reused-components/navigation.js";
import CustomChonky from "./CustomChonky.tsx";
import { CloseIcon, LessonIcon, DownloadIcon, ReturnIcon } from '../reused-components/icons';
import {TeacherOverviewList} from "../reused-components/TeacherComponents.js";
import {username, getMasterDownloadLinks, getAllDownloadData} from "../backend.js"
import {
  isMobile
} from "react-device-detect";
var mobileVersion = isMobile
var screenAspectRatio = window.innerHeight/window.innerWidth
var narrowBrowser = screenAspectRatio>0.55;

export class PortfolioPage extends React.Component {
  constructor(props) {
    super(props)
    this.state = {isExtended: "extended", page: "main", popUp: false}
  }
  changeState = () => {
    if (this.state.isExtended === "extended") {
      this.setState({isExtended: "not-extended"})
    }
    else {
      this.setState({isExtended: "extended"})
    }
  }
  openOtherPage = (page) => {
    this.setState({page: page})
  }
  render() {
    if (this.state.page === "download") {
      return <PortfolioDownloadPage changePage={(page) => this.setState({page:page})}/>
    }
    return (
      <>
        {(this.state.page === "master") ? <PortfolioMasterPage changePage={(page) => this.setState({page:page})}/> : (<><div style={{ position: "absolute" }}>
          <Navbar pageName="Your Portfolio" sideNavAnimation={this.changeState} />
        </div>
        <div style={{ position: "absolute", zIndex: -1 }} className={this.state.isExtended}>
          <div style={{ display: "inline-block", position: "absolute" }}>
            <SideNav isExtended={"extended"} />
            </div>
          <div className="todo-outer-div" style={((this.state.isExtended === "extended") ? {overflow: "hidden"} : {width: "100vw", overflow: "hidden"})}>
          <div style={{marginLeft: "40px"}}>
            <PointTrackers />
          </div>
          <div style={{width: "100%", height: "100%", position: "relative", marginTop: "3%"}}>
          <div style={{width: "70%", height: "100%", marginTop: "2%",marginLeft: "3%"}}>
            <h1 style={{marginLeft: "1%", }}>
            Startup Assets
            </h1>
            <div style={{height: "70%"}}>
            
              <CustomChonky disableDragAndDropProvider={false} type="files"/>
              
          </div>
          </div>
          
      
      
          </div>
          </div>
          
          <div>
          <PreviousCollectedField openOtherPage={this.openOtherPage} changePopUp={() => this.changePopUp("Download")}/>
          </div>
        </div></>)}
      </>
    )
  }
}

