import React from "react";
import { isClassActive} from "../backend";

export class JoinClassButton extends React.Component {
    constructor(props) {
        super(props)
        this.state = {visible: false}
        this.height = this.props.height
        const socket = new WebSocket('wss://cambio-socket.herokuapp.com/');
        socket.addEventListener('message',  (event) => {
          if (event.data === "start_class") {
              this.setState({visible: true})
          }
          else if (event.data === "end_class") {
            this.setState({visible: false})
          }
        });
        this.checkClassActive()
    }
    checkClassActive = async () => {
      var isActive = await isClassActive()
      this.setState({visible: isActive})
    }
    render() {
        if (!this.state.visible) {
          return <></>
        }
        return (<svg xmlns="http://www.w3.org/2000/svg" width="auto" height={this.height} viewBox="0 0 187 56" style={{cursor:"pointer", display: "inline-block"}}
        onClick={() => {
            window.location.href = "/#/class"
        }}
        >
        <rect id="Rectangle_1815" data-name="Rectangle 1815" width="187" height="56" rx="19" fill="#f8f8f8"/>
        <text id="Join_Class" data-name="Join Class" transform="translate(29 37)" font-size="22" font-weight="600"><tspan x="0" y="0">Join Class</tspan></text>
        <g id="school_black_24dp" transform="translate(135 9)">
          <path id="Path_221" data-name="Path 221" d="M0,0H38V38H0Z" fill="none"/>
          <path id="Path_222" data-name="Path 222" d="M7.333,19.118v6.333L18.417,31.5,29.5,25.452V19.118L18.417,25.167ZM18.417,3,1,12.5,18.417,22l14.25-7.774V25.167h3.167V12.5Z" transform="translate(0.583 1.75)" fill="#e0a124"/>
        </g>
      </svg>
      )
    }
}