import React from "react";
import { Navbar } from "../reused-components/navigation";
import {VideoQuestionPopup} from "../VideoQuestionPopup.js"
import {PointTrackers} from "../reused-components/pointTrackers.js"
function openInNewTab(href) {
    Object.assign(document.createElement('a'), {
      target: '_blank',
      href: href,
    }).click();
  }
export class ClassPage extends React.Component {
    constructor(props) {
        super(props)
        
        openInNewTab("https://us02web.zoom.us/j/5772259959")
    }
    render() {
        return (<div><Navbar pageName="Class" sideNavAnimation={this.changeState} />
            <div style={{marginLeft: "10vw", width: "100vw"}}><PointTrackers /></div>
            <div className="popup-clickable-background-div"></div>
            
            <div className="popup-background-div">
             <ClassWidget />  
            </div>
            </div>)
    }
}
/*
<VideoQuestionPopup 
      changePopUp={() => {
        this.props.unpauseVideo()
        this.props.changePopUp()
      }} videoQuestionData={this.props.videoQuestionData}/>*/
class ClassWidget extends React.Component {
    constructor(props) {
        super(props)
        const socket = new WebSocket('wss://cambio-socket.herokuapp.com/');
        this.state = {prompt: null}
        this.quizes = [
            {question: "How would you define entrepreneurship?", type: "written", points: 3, start: 81.9, completed: false}, 
            {question: "What makes Micah an entrepreneur?", type: "written", points: 3, start: 370, completed: false},   
            {start: 519.5, type: "multipleChoice", points: 6, completed: false, questions: [{question: "Are you willing to work 60+ hour weeks?", alternatives: ["YES", "NO"]},
              {question: "Do you feel comfortable selling something to someone?", alternatives: ["YES", "NO"]},
              {question: "Do you feel comfortable asking for money?", alternatives: ["YES", "NO"]},
              {question: "Do you think you can handle repeated negative feedback or rejection?", alternatives: ["YES", "NO"]},
              {question: "Are you willing to take on the responsibility of a CEO, and be held accountable for all company decisions? ", alternatives: ["YES", "NO"]},
              {question: "Do you think that you could fire somebody?", alternatives: ["YES", "NO"]},
              {question: "Do you want to work for a larger company, but try to lead innovation from within the company?", alternatives: ["YES", "NO"]},
              {question: "Do you want to start a small to medium sized business that makes enough to fund your family’s expenses?", alternatives: ["YES", "NO"]},
              {question: "Do you want to get funding from major investors, and scale your startup to hundreds of thousands of users?", alternatives: ["YES", "NO"]},
              {question: "Would you be willing to sacrifice your ownership or shares in your company for the sake of social good?", alternatives: ["YES", "NO"]},
          ]}]
          socket.addEventListener('message',  (event) => {
            if (event.data === "end_class") {
                window.location.href = "/#/"
            }
            this.setState({prompt: JSON.parse(event.data)})
          });
    }
    render() {
        if (this.state.prompt) {
            return <VideoQuestionPopup changePopUp={() => {
                this.setState({prompt: null})
            }}
            videoQuestionData={this.state.prompt}
            />
        }
        return <><div style={{width: "50vw", height: "30vw", backgroundColor: "white", borderRadius: "40px"}}></div>
        <div style={{height: "100%", width: "100%", display: "flex", alignItems: "center", justifyContent: "center", position: "absolute", fontSize: "30px"}}>
            <h1>Class has started!</h1>
        </div>
        </>
    }
}