import React from "react";
import { TeacherNavbar } from '../reused-components/navigation.js';
import {Link,} from "react-router-dom";
import {TeacherOverviewList} from "../reused-components/TeacherComponents.js";
import {StudentIcon} from "../reused-components/TeacherComponents.js"

export class TeacherSingleStudentOverviewPage extends React.Component {
    render(){
      return (
        <div>
          <TeacherNavbar/>
          <div style={{paddingTop: "3vh", paddingLeft: "9vw", position: "relative"}}>
          <Link to="/teacher/class/assignments" style={{textDecoration: "none"}}>
              <h1 style={{cursor: "pointer",color:"black"}}>Assignments</h1>
              </Link>
              <div style={{height: "70vh", width: "50vw", display: "inline-block"}}>
            <TeacherOverviewList type="assignment" link="/teacher/class/" suffix={"/"+this.props.studentName}/>
            </div >
            
            <div style={{position: "absolute", right: "7vw", top: "3vh"}}>
              <div style={{borderLeft: "3px solid #bbb", position: "fixed", right: "36vw", height: "70vh", marginTop: "10vh"}}></div>
              <Link to="/teacher/class/students" style={{textDecoration: "none"}}>
              <h1 style={{cursor:"pointer", color:"black"}} 
              >Info </h1>
              </Link>
              <div style={{ height: "32.5vh", width: "25vw"}}>
              <div className="teacher-overview-outer-div" style={{overflow: "hidden"}}>
                <div className="teacher-overview-item"><StudentIcon />   {this.props.studentName}</div>
                <div className="teacher-overview-item">Missed Assignments: 5!</div>
                <div className="teacher-overview-item">Grade: C</div>
              </div>
              </div>
              </div>
          </div>
        </div>
      )
    }
  }

