class Badges extends React.Component {
    constructor(props) {
      super(props);
      window.addEventListener("resize", this.hideBadges)
      this.state = { show: (window.innerWidth > 750), guest: false};
      this.guestMode()
    }
    hideBadges = () => {

      this.setState({ show: (window.innerWidth > 750) })
    }
    guestMode = async () => {
      var guest = await isGuest()

      this.setState({guest: guest})
    }
    render() {
      
      if (this.state.show) {
        return (<div className="badge-div" style={{cursor: "pointer"}} onClick={this.props.changePopUp}>
         {this.state.guest ?  <><h1 className="badge-text">Badges</h1>
          <Hexagon color="#EECC7C" />
          <Hexagon color="#7CA9EE" />
          <Hexagon color="#8BEE7C" />
          <Hexagon color="#EE7C7C" />
          <Hexagon color="white" /></> : null}
          
          <span style={{marginRight: "20px"}}><NotificationBell /></span>
          <span ><Link to="/login" >
            
            <button onClick={logout} style={{cursor: "pointer"}}>
          <LogoutIcon/>
          </button></Link></span>
        </div>)
      }
      else {
        return null;
      }
    }
  }
