import { TeacherNavbar } from '../reused-components/navigation.js';
import React from 'react';
import {Link} from "react-router-dom";

export class TeacherPage extends React.Component {
    render(){
      return (
        <div>
          <TeacherNavbar/>
          <div style={{padding: "10vh"}}>
            <h1>Classes</h1>
          {["Journey1"].map((className) => (<Link to={`/teacher/class`} style={{textDecoration: "none"}} >
              <div style={{height: "30vh", width: "40vh", color: "black"}}>
                <div style={{width: "100%", height: "60%", borderRadius: "3vh 3vh 0 0", backgroundColor: "#E66E6E"}}>
                <div style={{padding: "3vh"}}>{className}</div></div>
                <div style={{backgroundColor: "#f8f8f8", borderRadius: "0 0 3vh 3vh", height: "40%", width: "100%"}}></div>
                </div>
              </Link>))}
              </div>
        </div>
      )
    }
  }