var screenAspectRatio = window.innerHeight/window.innerWidth
var narrowBrowser = screenAspectRatio>0.55;




export class HomePage extends React.Component {
  constructor(props) {
    super(props)
    window.addEventListener("resize", () => this.changedBrowserSize())
    this.screenAspectRatio = window.innerHeight/window.innerWidth
    narrowBrowser = this.screenAspectRatio>0.55;  
    if (mobileVersion) {
      narrowBrowser = false
    }
    this.state = { isExtended: ((mobileVersion || narrowBrowser) ? "not-extended" : "extended"), popUp: false, narrowBrowser: narrowBrowser }
    //getRequestToDoList()

    this.refreshDownloadData();
  }
  changedBrowserSize = () => {
    this.screenAspectRatio = window.innerHeight/window.innerWidth
    narrowBrowser = this.screenAspectRatio>0.55;
    if (this.screenAspectRatio>0.77) {
      mobileVersion=true
    } 
    else if (!isMobile && this.screenAspectRatio<0.77){
      mobileVersion=false
    }
    this.setState({narrowBrowser: narrowBrowser})
  }
  refreshDownloadData = async () => {
    var downloadData = await loadDownloadData();

    this.setState((props, state) => {
      return ({ downloadData: downloadData })
    })
  }
  changeState = () => {
    if (this.state.isExtended === "extended") {
      this.setState((props, state) => {
        return { isExtended: "not-extended" }
      })
    }
    else {
      this.setState((props, state) => {
        return { isExtended: "extended" }
      })
    }
  }
  changePopUp = (type = "none") => {
    this.setState({popUp: !this.state.popUp, popUpType: type, videoQuestionData: null })
  }
  openVideoQuestionPopup = (type = "none", data, unpauseVideo) => {
    this.unpauseVideo = unpauseVideo
    this.setState({popUp: true, videoQuestionData: data, popUpType: type})
  }
  popUp() {
    if (!this.state.popUp) {
      return;
    }
    if (this.state.videoQuestionData) {
      return (<PopUp changePopUp={this.changePopUp} type={this.state.popUpType} videoQuestionData={this.state.videoQuestionData} unpauseVideo={this.unpauseVideo}/>)
    }
    return (<PopUp changePopUp={this.changePopUp} type={this.state.popUpType} downloadData={this.state.downloadData} />)
  }
  render() {
    this.screenAspectRatio = window.innerHeight/window.innerWidth
    var loadingBarList = [1, 3, 2];
    var width = "50vw";
    return (
      <div style={mobileVersion ? { overflowY: "scroll" } : {}} key={JSON.stringify(this.state.narrowBrowser)}>
        
        <div style={{ position: "fixed" }}><Navbar pageName="Home" sideNavAnimation={this.changeState} /></div>
        <div style={{ position: "fixed", zIndex: -1 }} className={this.state.isExtended}>
          <div style={(mobileVersion || narrowBrowser) ? { display: "block", position: "absolute", left: "0", zIndex: 10 } : { display: "inline-block", position: "absolute" }}>
            <SideNav isExtended={this.state.isExtended} /></div>
          <div className="video-outer-div" style={mobileVersion ? {
            position: "fixed", bottom: "auto", top: "25vh", right: "5vw", overflowY: "scroll"
            , height: "calc( 100vh - 50px)",
          } : (narrowBrowser ? {position: "fixed", left: "-200px", bottom: "0"}: null)}>
            <div style={{ paddingBottom: "5vh" }}>
              <PointTrackers />
              <div style={mobileVersion ? { width: "90vw", aspectRatio: "16/9" } : { width: (narrowBrowser ? "65vw" :width.toString()), aspectRatio: "16/9", maxHeight: "60vh" }}>
                <VideoPlayer openVideoQuestionPopup={this.openVideoQuestionPopup}/>
              </div>
              <div style={mobileVersion ? { width: "100%" } : { width: width.toString, marginTop: "calc(4vh - 20px)" }}>
                <div style={{ display: "inline-block" }}>
                  <h1 style={{ fontSize: "3vh" }}>Startup: Company inc.</h1>
                  <h1 style={{ fontSize: "2vh" }}>Entrepreneur:   Name Nameson</h1>
                </div>
                <div style={mobileVersion ? { display: "block" } : { display: "inline-block", float: "right" }}>
                  <div style={mobileVersion ? { backgroundColor: "#E8E8E8", height: "13vh", width: "90vw", marginTop: "2vh", borderRadius: "2vh" }
                    : { backgroundColor: "#f4f4f4", height: "13vh", width: (narrowBrowser ? "40vw":"30vw"), marginTop: "2vh", borderRadius: "2vh" }}>
                    <div style={{ justifyContent: "space-evenly", display: "flex", transform: "translateY(1.5vh)" }}>
                      <button onClick={() => this.changePopUp("download")} style={{ cursor: "pointer" }}>
                        <DCMButton size="20px" loadingBarCompletion={loadingBarList[0]} text="Download" icon={<DownloadIcon size="4vh" color="white" />} narrowBrowser={this.narrowBrowser }/>
                      </button>
                      <button onClick={() => this.changePopUp("challenge")} style={{ cursor: "pointer" }}>
                        <DCMButton size="100px" loadingBarCompletion={loadingBarList[1]} text="Challenge" icon={<HatchetIcon size="3vh" colors={["white","#965594"]} narrowBrowser={this.narrowBrowser }/>} />
                      </button>
                      <button style={{ cursor: "pointer" }} onClick={() => this.changePopUp("master")}>
                        <DCMButton size="100px" loadingBarCompletion={loadingBarList[2]} text="Master" icon={<MasterIcon size="3.5vh" colors={["white","#965594"]} narrowBrowser={this.narrowBrowser }/>} />
                      </button>

                    </div>
                  </div>
                </div>
              </div>
              {mobileVersion ? <div style={{ marginTop: "2vh" }}><InformationField changePopUp={this.changePopUp} downloadData={this.state.downloadData} /></div> : null}
            </div>
          </div>
        </div>
        {mobileVersion ? null : <InformationField changePopUp={this.changePopUp} downloadData={this.state.downloadData} />}
        {this.popUp()}
      </div>
    )
  }
}


class PopUp extends React.Component {

  render() {
    return <div className="popup-background-div">
      <div className="popup-clickable-background-div" onClick={(this.props.type === "videoQuestion") ? null: this.props.changePopUp}>
      </div>
      {(this.props.type === "challenge") ? <ChallengePopUp changePopUp={this.props.changePopUp} downloadData={this.props.downloadData} /> : null}
      {(this.props.type === "master") ? <MasterPopUp changePopUp={this.props.changePopUp} downloadData={this.props.downloadData} /> : null}
      {(this.props.type === "download") ? <DownloadPopUp changePopUp={this.props.changePopUp} downloadData={this.props.downloadData} /> : null}
      {(this.props.type === "knowledgeNuggets") ? <KnowledgeNuggetsPopUp changePopUp={this.props.changePopUp} downloadData={this.props.downloadData} /> : null}
      {(this.props.type === "fromTheArchitects") ? <FromTheArchitectsPopUp changePopUp={this.props.changePopUp} downloadData={this.props.downloadData} /> : null}
      {(this.props.type === "videoQuestion") ? <VideoQuestionPopup 
      changePopUp={() => {
        this.props.unpauseVideo()
        this.props.changePopUp()
      }} videoQuestionData={this.props.videoQuestionData}/> : null}
    </div>
  }
}

class ChallengePopUp extends React.Component {
  constructor(props) {
    super(props);
    this.state = { challenges: ["loading..."] }

  }
  renderChallenges() {
    var lst = (this.props.downloadData.challenges ? this.props.downloadData.challenges : this.state.challenges)
    return lst.map(text => (<div style={{ marginBottom: "20px"}}>
      <CheckIcon /><p1 style={{ marginLeft: "15px"}}>
        {text}</p1></div>))
  }
  render() {
    return (
      <div style={{minWidth: "30vw", zIndex: 2, maxWidth: "70vw"}}>
      <div style={{ minWidth: "30vw", backgroundColor: "#8b4389", opacity: "100%", zIndex: 2, borderRadius: "20px 20px 0 0 ", paddingBottom: "5px", paddingTop: "5px" }}>
        <button style={{ cursor: "pointer", float: "right", marginTop: "15px", marginRight: "15px" }} onClick={this.props.changePopUp}>
          <CloseIcon color="white"/></button>
        <h1 style={{ marginLeft: "20px", color: "white" }}><HatchetIcon size="40" colors={["white","#965594"]} />Challenges</h1></div>
      <div style={{ minWidth: "30vw", backgroundColor: "white", opacity: "100%", zIndex: 2, borderRadius: "0 0 20px 20px ", paddingBottom: "30px", paddingTop: "50px" }}>
        <div style={{marginLeft: "30px", paddingRight: "30px"}}>
          {this.renderChallenges()}
          <div style={{ position: "relative", height: "50px" }}>
            <div style={{ position: "absolute" }}><button style={{
              backgroundColor: "#965594",
              color: "white", marginLeft: "10px", padding: "7px", borderRadius: "5px", cursor: "pointer"}}
              onClick={this.props.changePopUp}
            >Accept Challenges</button></div>
          </div>
          </div>
        </div>
      </div>
    )
  }
}
class MasterPopUp extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      challenges: ["loading..."],
      selectedFiles: []
    }

  }
  onChangeHandler = event => {
    var selectedFiles = this.state.selectedFiles
    selectedFiles.push(event.target.files[0])
    this.setState({
      selectedFiles: selectedFiles,

    })
    
  }
  removeUploadFile = (index) => {
    var selectedFiles = this.state.selectedFiles
    selectedFiles.splice(index, 1)
    this.setState({selectedFiles: selectedFiles})
  }
  uploadClickHandler = () => {
    this.state.selectedFiles.forEach( file =>{
    uploadMasterAssignment(file, this.props.downloadData.checkpoint, "useless_word_should_be_removed")})
    this.props.changePopUp()
  }
  render() {
    return (
      <div style={{minWidth: "30vw", zIndex: 2}}>
      <div style={{ minWidth: "30vw", backgroundColor: "#8b4389", opacity: "100%", zIndex: 2, borderRadius: "20px 20px 0 0 ", paddingBottom: "5px", paddingTop: "5px" }}>
        <button style={{ cursor: "pointer", float: "right", marginTop: "15px", marginRight: "15px" }} onClick={this.props.changePopUp}>
          <CloseIcon color="white"/></button>
        <h1 style={{ marginLeft: "20px", color: "white" }}><MasterIcon size="40" colors={["white","#965594"]} />Master</h1></div>
      <div style={{ minWidth: "30vw", backgroundColor: "white", opacity: "100%", zIndex: 2, borderRadius: "0 0 20px 20px ", paddingBottom: "50px", paddingTop: "50px" }}>
            <div style={{ position: "relative", marginLeft: "30px" }}>
              {this.state.selectedFiles.map((file, index )=> <div>
                {file.name}
                <div onClick={() => this.removeUploadFile(index)} style={{display: "inline-block",transform:"translateY(5px)"}}>
                  <CloseIcon />
                  </div>
                  
                  </div>)}
              <input type="file" name="file" onChange={this.onChangeHandler} /><button style={{
                backgroundColor: "#965594",
                color: "white", marginLeft: "10px", padding: "7px", borderRadius: "5px", cursor: "pointer"}}
                onClick={this.uploadClickHandler}
              >Submit Assignments</button></div>
          </div>
        </div>

      
    )
  }
}
class DownloadPopUp extends React.Component {
  constructor(props) {
    super(props);
    this.state = { downloadData: this.props.downloadData }
  }
  renderDownload() {
    //Have to change code when download data api gets updated from string to list.
    return this.state.downloadData.download_titles.map((text, index) => (<div style={{ marginBottom: "20px" }}>
      <p1 style={{ marginLeft: "30px", paddingRight: "30px" }}>
        {text}</p1><a href={this.state.downloadData.download_urls[index]} target="_blank"><button style={{
          backgroundColor: "#965594",
          color: "white", marginLeft: "10px", padding: "7px", borderRadius: "5px", cursor: "pointer"}}
          onClick={this.props.changePopUp}
        >Get File</button></a></div>))
  }
  render() {
    return (<div style={{minWidth: "30vw", zIndex: 2}}>
    <div style={{ minWidth: "30vw", backgroundColor: "#8b4389", opacity: "100%", zIndex: 2, borderRadius: "20px 20px 0 0 ", paddingBottom: "5px", paddingTop: "5px" }}>
      <button style={{ cursor: "pointer", float: "right", marginTop: "15px", marginRight: "15px" }} onClick={this.props.changePopUp}>
        <CloseIcon color="white"/></button>
      <h1 style={{ marginLeft: "20px", color: "white" }}><DownloadIcon color="white" />Download</h1></div>
    <div style={{ minWidth: "30vw", backgroundColor: "white", opacity: "100%", zIndex: 2, borderRadius: "0 0 20px 20px ", paddingBottom: "30px", paddingTop: "50px" }}>
      {this.renderDownload()}
    
      {/* <div style={{ position: "absolute" }}>
        
        </div> */}
    </div>
    </div>
    )
  }
}

class VideoPlayer extends React.Component {
  constructor(props) {
    super(props)
    this.state = { video_url: null, playing: false, index: 0}
    
    //Change this stuff later when seb gives you the stuff
    this.quizes = [
    {question: "How would you define entrepreneurship?", type: "written", points: 5, start: 81.9, completed: false}, 
    {question: "What makes Micah an entrepreneur?", type: "written", points: 5, start: 370, completed: false},   
    {start: 519.5, type: "multipleChoice", points: 5, completed: false, questions: [{question: "Are you willing to work 60+ hour weeks?", alternatives: ["YES", "NO"]},
      {question: "Do you feel comfortable selling something to someone?", alternatives: ["YES", "NO"]},
      {question: "Do you feel comfortable asking for money?", alternatives: ["YES", "NO"]},
      {question: "Do you think you can handle repeated negative feedback or rejection?", alternatives: ["YES", "NO"]},
      {question: "Are you willing to take on the responsibility of a CEO, and be held accountable for all company decisions? ", alternatives: ["YES", "NO"]},
      {question: "Do you think that you could fire somebody?", alternatives: ["YES", "NO"]},
      {question: "Do you want to work for a larger company, but try to lead innovation from within the company?", alternatives: ["YES", "NO"]},
      {question: "Do you want to start a small to medium sized business that makes enough to fund your family’s expenses?", alternatives: ["YES", "NO"]},
      {question: "Do you want to get funding from major investors, and scale your startup to hundreds of thousands of users?", alternatives: ["YES", "NO"]},
      {question: "Would you be willing to sacrifice your ownership or shares in your company for the sake of social good?", alternatives: ["YES", "NO"]},
  ]},
    
    ]
    this.refreshVideoPlayer()
  }
  refreshVideoPlayer = async () => {
    var newState = await loadDownloadData()
    this.baseUrl = newState.video_url
    this.setState((props, state) => {
      return newState
    })
  }
  handleVideoProgress = (time) => {
    /*if (!this.quizes[this.state.index]) {
      return
    }*/
    this.quizes.forEach((item, i) =>{
    if ((time.playedSeconds > this.quizes[i].start) && (time.playedSeconds < this.quizes[i].start+1) && !this.quizes[i].completed) {
      this.props.openVideoQuestionPopup("videoQuestion", this.quizes[i], this.unpauseVideo)
      this.quizes[i].completed =  true;
      this.setState({playing: false,// index: this.state.index + 1
      })
    }})
  }
  unpauseVideo = () => {
    this.setState({playing: true})
  }
  render() {

    return (<>
      {(this.state.video_url ? 
      <ReactPlayer url={this.state.video_url} width="100%" height="100%" controls={true} playing={this.state.playing} onProgress={(time) => this.handleVideoProgress(time)} 
        onEnded={() => {/*
          this.setState({video_url: null})
          this.refreshVideoPlayer()*/
        }}
        onPlay={() => this.setState({ playing: true })}
        onPause={() => this.setState({ playing: false })}
        config={{youtube: {playerVars: {disablekb: 0, modestbranding: 1}}}}
      />
      
      : <div style={{width: "100%", height: "100%", display: "flex", 
      justifyContent:"center", alignItems: "center", backgroundColor: "black"
      
      }}><div style={{height: "25%", width:"15%"}}><Loader type="Oval" color="#00BFFF" height={"100%"} width={"100%"} />
      
      </div></div>)}
    </>)
  }
}
class KnowledgeNuggetsPopUp extends React.Component {
  constructor(props) {
    super(props);
    this.state = { downloadData: this.props.downloadData }
  }
  renderDownload() {
    return (<div style={{ marginBottom: "20px", paddingLeft: "30px", paddingRight: "30px"}}>
      {this.state.downloadData.knowledge_nugget.split("\r\n\r\n").map(line => <div style={(line === " ")? {height: "15px"}: null}>{line}</div>)}</div>)
  }
  render() {
    return (
    <div style={{minWidth: "30vw", zIndex: 2, maxWidth: "800px"}}>
    <div style={{ minWidth: "30vw", backgroundColor: "#8b4389", opacity: "100%", zIndex: 2, borderRadius: "20px 20px 0 0 ", paddingBottom: "5px", paddingTop: "5px" }}>
      <button style={{ cursor: "pointer", float: "right", marginTop: "15px", marginRight: "15px" }} onClick={this.props.changePopUp}>
        <CloseIcon color="white"/></button>
      <h1 style={{ marginLeft: "20px", color: "white" }}><LightbulbIcon />Knowledge Nuggets</h1></div>
    <div style={{ minWidth: "30vw", backgroundColor: "white", opacity: "100%", zIndex: 2, borderRadius: "0 0 20px 20px ", paddingBottom: "50px", paddingTop: "50px", maxWidth: "85vw"}}>
      {this.renderDownload()}
    </div>
    </div>
    )
  }
}

class FromTheArchitectsPopUp extends React.Component {
  constructor(props) {
    super(props);
    this.state = { downloadData: this.props.downloadData }
  }
  renderDownload() {
    return (<div style={{ marginBottom: "20px", paddingLeft: "30px", paddingRight: "30px"}}>
    {this.state.downloadData.notes.split("\r\n\r\n").map(line => <div>{line}</div>)}</div>)
  }
  render() {
    return (<div style={{minWidth: "30vw", zIndex: 2}}>
    <div style={{ minWidth: "30vw", backgroundColor: "#8b4389", opacity: "100%", zIndex: 2, borderRadius: "20px 20px 0 0 ", paddingBottom: "5px", paddingTop: "5px" }}>
      <button style={{ cursor: "pointer", float: "right", marginTop: "15px", marginRight: "15px" }} onClick={this.props.changePopUp}>
        <CloseIcon color="white"/></button>
      <h1 style={{ marginLeft: "20px", color: "white" }}><ArchitehtCompassIcon />From the Architects</h1></div>
    <div style={{ minWidth: "30vw", backgroundColor: "white", opacity: "100%", zIndex: 2, borderRadius: "0 0 20px 20px ", paddingBottom: "50px", paddingTop: "50px" }}>
      {this.renderDownload()}
    </div>
    </div>
    )
  }
}


class PathwayAndCheckpoints extends React.Component {
  constructor(props) {
    super(props)
    this.state = { pathway: "loading...", checkpoint: "loading..." }
    this.refreshPathwayAndCheckpoints()
  }
  refreshPathwayAndCheckpoints = async () => {
    var newState = await loadDownloadData()
    this.setState((props, state) => {
      return newState
    })
  }
  render() {
    return (<div style={{maxWidth: "90%", maxHeight: "30%"}}><svg xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" width={mobileVersion ? "70vw" : "auto"} height="auto" viewBox="0 0 331 355">
      <rect id="Rectangle_1588" data-name="Rectangle 1588" width="331" height="355" rx="30" fill="#fff" />
      <text id="Pathway:" transform="translate(63.5 63.918)" font-size="25"  font-weight="600"><tspan x="0" y="0">Checkpoint:</tspan></text>
      <text id="Pathway_name" data-name="Pathway name" transform="translate(25 124.209)" font-size="25"  font-weight="600"><tspan x="0" y="0">{this.state.checkpoint_name}</tspan></text>

      <foreignObject transform="translate(31.5 230.291)" width="80%" height="100%"><div 
      style={{fontSize:"1.6rem", fontWeight:"600", width: "100%"}}
      >{this.state.description}</div></foreignObject>
      <text id="Checkpoint:" transform="translate(63.5 211.791)" font-size="25"  font-weight="600"><tspan x="0" y="0">Lesson:</tspan></text>
      <line id="Line_15" data-name="Line 15" x2="295" transform="translate(21.5 157.5)" fill="none" stroke="#707070" stroke-width="1" />
      <image id="icons8-journey-96" width="35" height="35" transform="translate(25 185)" xlinkHref="data:image/svg+xml,%3Csvg xmlns='http://www.w3.org/2000/svg' height='35px' viewBox='0 0 24 24' width='35px' fill='%23000000'%3E%3Cpath d='M0 0h24v24H0z' fill='none'/%3E%3Cpath d='M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm-2 14.5v-9l6 4.5-6 4.5z'/%3E%3C/svg%3E" />
      <image id="icons8-map-pinpoint-96" width="35" height="35" transform="translate(22 35)" xlinkHref="data:image/png;base64," />
    </svg></div>
    )
  }
}


class InformationField extends React.Component {
  render() {
    return (
      <div style={mobileVersion ? {
        width: "90vw", backgroundColor: "#f9f9f9", borderRadius: "6vh"
        , zIndex: "-20", position: "relative", display: "block"
      } : {
        position: "fixed", right: "0px", bottom: "0px", zIndex: "-20", height: "calc(90vh - 100px)", width: (narrowBrowser ? "25vw":"calc(22vw - 50px)"),
        borderRadius: "6vh 0 0 0", backgroundColor: "#f9f9f9"
      }}>
        <div style={{ marginLeft: "20px", marginTop: "30px" }}>
          <KnowledgeNuggets expand={() => this.props.changePopUp("knowledgeNuggets")} />
          <FromTheArchitects expand={() => this.props.changePopUp("fromTheArchitects")} />
          <PathwayAndCheckpoints downloadData={this.props.downloadData} />
        </div>

      </div>
    )
  }
}



