class CheckpointButton extends React.Component {
  render() {
    return (
      <div style={{backgroundColor: "#f8f8f8", marginTop: "5vh", borderRadius: "2vh", display: "flex", 
      alignItems: "center", height: "11vh", width: "65%", paddingLeft: "5%", cursor: "pointer"}}>
        <CheckpointIcon /> <h1 style={{display: "inline-block"}}>Checkpoint </h1>
      </div>
    )
  }
}


