import React from "react";
//import "./style.css";
import logo from "../logo.png";
import {getRequestToDoList, signUp} from "../backend.js";
import "../loginPage.css";

import {
    isMobile
  } from "react-device-detect";


function LoginButton(props) {
    return <button style={{cursor: "pointer"}} onClick={async () => {
        //errorMessage= "Wrong password or username";
        console.log(props.info)
        if (props.info.password != props.info.password2) {
            changeErrorMessage("Passwords don't match")
            console.log("passwords dont match")
            
        }
        else {if (await signUp(props.info)) {
            console.log("signup succesful")
            window.location.replace("/#/login");
        }
        else {
            console.log("signup failed")
            changeErrorMessage("Something went wrong")
        }
        }}}>
        <svg xmlns="http://www.w3.org/2000/svg" width="98" height="40" viewBox="0 0 180 66">
<rect id="Rectangle_1585" data-name="Rectangle 1585" width="180" height="66" rx="19" fill="#8b4389"/>
<text id="Login" transform="translate(21 45)" fill="#fff" font-size="40" ><tspan x="0" y="0">Sign up</tspan></text>
</svg>
</button>;
}
