          <path id="Path_62" data-name="Path 62" d="M0-233.939s82.909,3.878,99.879,84.032S253.99-47.124,310.788-45.19c101.783,3.465,200.477-38.444,310.3,0C826.757,26.8,822.667,178.788,822.667,178.788H0Z" transform="translate(-6 901.212)" fill="#8b4389"/>
