import React from "react";
import { CloseIcon } from "./icons";
import { closePopup, openPopup } from "./Popup";
import { isMobile } from "react-device-detect";
import { submitFeedbackBackend } from "../backend";
export class FeedbackButton extends React.Component {
    constructor(props) {
        super(props)
        this.state = {showPopup: false}
        this.height = this.props.height
        if (!this.height) {
            this.height = "4vh"
        }
    }
    displayPopUp = () => {
        openPopup(<FeedbackPopUp />)
    }
    render() {
        return (<>
        
        <svg xmlns="http://www.w3.org/2000/svg" width="auto" height={this.height} viewBox="0 0 187 56" onClick={this.displayPopUp} style={{cursor:"pointer", display: "inline-block"}}>
  <rect id="Rectangle_1808" data-name="Rectangle 1808" width="187" height="56" rx="19" fill="#f8f8f8"/>
  <text id="Feedback" transform="translate(29 37)" font-size="22" font-family="SegoeUI-Semibold, Segoe UI" font-weight="600"><tspan x="0" y="0">Feedback</tspan></text>
  <path id="Path_217" data-name="Path 217" d="M17.143,2a14.143,14.143,0,1,0,0,28.286h.832v4.992c8.086-3.893,13.311-11.647,13.311-19.135A14.152,14.152,0,0,0,17.143,2Zm1.664,24.126H15.479V22.8h3.328Zm0-5.824H15.479c0-5.408,4.992-4.992,4.992-8.319a3.328,3.328,0,1,0-6.656,0H10.487a6.656,6.656,0,0,1,13.311,0C23.8,16.143,18.807,16.559,18.807,20.3Z" transform="translate(136.017 11)" fill="#246fe0"/>
</svg>

      </>
      )
    }
}



class FeedbackPopUp extends React.Component {
    constructor(props) {
        super(props)
        this.state = {value: null}
    }
    handleChange = (event) => {
        this.setState({value: event.target.value});
      }
    submitFeedback = async () =>  {
        //console.log(window.location.href)
        //console.log(this.state.value)
        var feedback = {"bug_page": window.location.href, "feedback": this.state.value}
        await submitFeedbackBackend(feedback)
        closePopup()
    }
    render() {
        return (
        <div style={{minWidth: "30vw", zIndex: 2}}>
      
      <div style={{ minWidth: "30vw", backgroundColor: "white", opacity: "100%", zIndex: 2, borderRadius: "20px 20px 20px 20px ", paddingBottom: "60px", paddingRight: "30px"}}>
      <h1 style={{ marginLeft: "30px", display: "inline-block"}}>Feedback</h1>
      <button style={{ cursor: "pointer", float: "right", marginTop: "15px"}} onClick={closePopup}>
          <CloseIcon/></button>
        
            <div style={{ marginLeft: "30px" }}>
            <textarea placeholder="What would you like us to improve?" style={{borderRadius: "15px", padding: "30px",border: "2px solid lightgrey", resize: "none", width: (isMobile? "60vw": "40vw"), height: "170px"}}
                    onChange={this.handleChange}
                    value={this.state.value}
                    >
                        </textarea>
                        </div>
                        <div style={{float: "right", marginTop: "10px"}}>
                        <button style={{marginRight: "20px", color: "white"}} className="cancel-button"
                                onClick={closePopup}
                                >
                                    Cancel
                                </button>
                        <button style={{backgroundColor: "#30e024", marginRight: "20px", color: "white"}} className="add-task-button"
                                 onClick={this.submitFeedback}
                                >

                                    Submit
                                </button>
                        </div>
              
          </div>
        </div>

        )
    }
}