import React from "react";
import { closePopup, openPopup } from "./Popup";
export class RewardScreen extends React.Component {
    render() {
        return (
        <div style={{filter: "brightness(200%)", color: "white"}} onClick={closePopup}>
            <span style={{display: "inline-block"}}><img style={{display: "inline-block"}} src="https://cliply.co/wp-content/uploads/2021/09/CLIPLY_372109170_FREE_FIREWORKS_400.gif"/></span>
            <span style={{display: "inline-block"}}>

                <div style={{transform: "translateY(-100px)", textAlign: "center"}}>
                    <h1>Congratulations!</h1>
                    <div></div>
                <h1>You got {this.props.points} points</h1>
                <h1>from {this.props.reason}!</h1>
                </div>
            </span>
            <span style={{display: "inline-block"}}><img style={{display: "inline-block"}} src="https://cliply.co/wp-content/uploads/2021/09/CLIPLY_372109170_FREE_FIREWORKS_400.gif" /></span>
        </div>
        )
    }
}
export function showRewardScreen(points, reason) {
    openPopup(<RewardScreen points={points} reason={reason} />)
}