import React, { useState } from "react";
import { DragDropContext, Droppable, Draggable, } from "react-beautiful-dnd";
import {PlussIcon, TrashIcon, EditIcon} from "../../reused-components/icons.js";
import "../../style.css"
import "../../reused-components/navigation.css";
import Calendar from 'react-calendar';
import './Calendar.scss';
import {getTeamMembers} from "../../backend.js";
import Slider from 'react-input-slider';
//import { Dropdown } from 'react-bootstrap';

function removeByIndex(array, index) {
    var temp_array = [];
    array.forEach((item, i) => {
      if (index !== i) {
        temp_array.push(item)
      }
    })
    return temp_array
  }

const reorder = (list, startIndex, endIndex) => {

    const result = Array.from(list);
    const [removed] = result.splice(startIndex, 1);
    result.splice(endIndex, 0, removed);
  
    return result;
  };
  

  const grid = 8;
  
  const getItemStyle = (isDragging, draggableStyle) => ({
    userSelect: 'none',
    margin: `0 0 ${grid}px 0`,
    position: "relative",
    ...draggableStyle
  });
  
  const getListStyle = isDraggingOver => ({
    background: "white",
    padding: grid,
    width: 200,
    marginLeft: "-7px",
  });
  
class CloseIcon extends React.Component {
    render() {
      return (<svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 0 24 24" width="24px" fill="#000000"><path d="M0 0h24v24H0z" fill="none" /><path d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12z" /></svg>)
    }
}
export class ToDoEditPopUp extends React.Component {
    constructor(props) {
      super(props);
      this.state = {actionSteps: this.props.state[this.props.indexes[0]][this.props.indexes[1]].actionSteps,
      taskName: this.props.item.content
      }
    }
    editToDoProperty = (propertyName, value) => {
      var newState = this.props.state;
      newState[this.props.indexes[0]][this.props.indexes[1]][propertyName] = value;
      this.props.updateToDo(newState)
      if (propertyName === "actionSteps") {
        this.setState({actionSteps: value})
      }
  
    }
    changeName = () => {
      var newName = prompt("New Task Name", this.props.item.content)
      if (newName) {
        this.editToDoProperty("content", newName)
        this.setState({taskName: newName})
      }
      
    }
    deleteItem = () => {
      var newState = this.props.state;
      newState[this.props.indexes[0]] = removeByIndex(newState[this.props.indexes[0]], this.props.indexes[1])
      this.props.updateToDo(newState)
      this.props.changePopUp()
    }
    render() {
      var toDoItem = this.props.state[this.props.indexes[0]][this.props.indexes[1]];

      return (<div style={{ minWidth: "500px", backgroundColor: "white", opacity: "100%", zIndex: 2, borderRadius: "20px", paddingBottom: "50px", 
      paddingLeft: "20px", paddingRight: "20px", paddingTop: "5px", height: "80vh", position: "relative"}}>
        <button style={{ cursor: "pointer", float: "right", marginTop: "15px", marginRight: "15px" }} onClick={this.props.changePopUp}>
          <CloseIcon /></button>
          <h1 style={{ marginLeft: "20px", fontSize: "25px", cursor: "pointer" }} onClick={this.changeName}>{this.state.taskName}  
          <div style={{display: "inline-block", cursor: "pointer", marginLeft: "20px", marginRight: "10px"}} onClick={this.changeName}>
            <EditIcon />
          </div> 
          <div style={{display: "inline-block", cursor: "pointer"}} onClick={this.deleteItem}>
            <TrashIcon />
          </div>
          </h1>
          
            
            <div style={{display: "inline-block", marginBottom: "10px", marginLeft: "20px"}}>
             <div style={{marginBottom: "10px"}}> Assign Teammate</div>
            <TeamSelect editToDoProperty={this.editToDoProperty}
              toDoItem={toDoItem}/>

            {/*<input style={{width: "70px", padding: "5px", height: "40px"}} placeholder="user" 
            onChange={(text) => this.editToDoProperty("assignedUser", text.target.value)}/>*/}
            <div style={{marginTop: "20px"}}>
            <DifficultySliders editToDoProperty={this.editToDoProperty} toDoItem={toDoItem} />
            </div>
            </div><div style={{display: "inline-block", marginLeft: "50px"}}>
            <div style={{marginBottom: "50px"}}>
              <div style={{marginBottom: "10px"}}>
            Due Date
            </div>
              <ScheduleDueDateButton editToDoProperty={this.editToDoProperty}
              toDoItem={toDoItem}
              />
            </div>
            <DurationSliders editToDoProperty={this.editToDoProperty} toDoItem={toDoItem}/>
            </div>
            <div key={JSON.stringify(this.state.actionSteps)} style={{marginLeft: "20px"}}>
              <h1 style={{fontSize: "20px"}}>Action Steps</h1>
              <div style={{maxHeight: "30vh", overflowX: "hidden", transform: "translateY(-2vh)"}} className="scrollingThing">
              <ActionStepsDraggable state={this.state.actionSteps} editToDoProperty={this.editToDoProperty}/>
              </div>
            </div>
            <div style={{transform: "translateY(-2vh)", marginLeft: "20px"}}>
            <ActionStepAddButton item={this.props.state[this.props.indexes[0]][this.props.indexes[1]]} editToDoProperty={this.editToDoProperty}/>
            </div>
      </div>
      )
    }
}
  

  
  
class ActionStepAddButton extends React.Component {
    constructor(props) {
      super(props);
      this.state = {isAdding: false, text: ""};
    }
    handleAddTask = () => {
      this.setState((props, state) => {
        return {isAdding: !this.state.isAdding};
      });
    }
    
    changeText = (text) => {
      this.setState({text: text})
    }
    addText = (e) => {
      if (e.keyCode === 13){
      console.log("oidsjfosdjfojfsd")
      var tempItem = this.props.item.actionSteps;
      tempItem.push([this.state.text, false])
      this.props.editToDoProperty("actionSteps", tempItem)
      this.handleAddTask()
    }
      
      
    }
    renderAddSection() {
      if (!this.state.isAdding) {
        return (<button onClick={this.handleAddTask}>
          <div style={{transform: "translateY(10px)"}}><PlussIcon size="15px" /> Add Task</div>
        </button>);
      }
      return (<div>
        <input value={this.state.text} onChange={(text) => this.changeText(text.target.value)} onKeyDown={(e) => this.addText(e)} />
      <div style={{transform: "translateY(10px)"}}>
        <button onClick={() => this.addText({keyCode: 13})} className="add-task-button" style={{width: "130px"}}>
          <p1 className="add-button-text">
              Add Action Step
          </p1>
        </button>
        <button onClick={this.handleAddTask} className="cancel-button">
        <p1 className="add-button-text">
            Cancel
          </p1>
        </button>
        </div>
      </div>);
    }
  
    render () {
      return (<div>
      {this.renderAddSection()}
      </div>)
    }
}
  
  
function makeActionStepsList(list) {
    var tempList = list.map((item, index) => ({
      content: item[0],
      id: `actionSteps ID ${index}`,
      isComplete: item[1]
    }))
    return tempList
}
  
function makeRegularActionsStepsList(list) {
    return list.map(item => [item.content, item.isComplete])
}
function ActionStepsDraggable(props) {
    const [state, setState] = useState([makeActionStepsList(props.state)]);
  
    function onDragEnd(result) {
      const { source, destination } = result;
  
      // dropped outside the list
      if (!destination) {
        return;
      }
      const sInd = +source.droppableId;
      const dInd = +destination.droppableId;
  
      if (sInd === dInd) {
        const items = reorder(state[sInd], source.index, destination.index);
        const newState = [...state];
        newState[sInd] = items;
        props.editToDoProperty("actionSteps",makeRegularActionsStepsList(newState[0]))
        setState(newState);
      }
    }
    return (
  
      <div style={{position: "relative", marginLeft: "-50px"}}>
        <div style={{ display: "flex" }}>
          <DragDropContext onDragEnd={onDragEnd}>
            
            {state.map((el, ind) =>{ 
              return (<div style={{marginLeft: "50px", userSelect: "none"}}>
  
                <div>
              <Droppable key={ind} droppableId={`${ind}`}>
                {(provided, snapshot) => (
                  <div
                    ref={provided.innerRef}
                    style={getListStyle(snapshot.isDraggingOver)}
                    //{width: "35vw", backgroundColor: "green"}}
                    {...provided.droppableProps}
                  >
                    <div style={{ position: "relative", height: "340px", width: "35vw", overflowY:"auto", overflowX: "hidden"}}>
                    {el.map((item, index) => {
                      return(
                      <Draggable
                        key={item.id}
                        draggableId={item.id}
                        index={index}
                      >
                        {(provided, snapshot) => (
                          <div
                            ref={provided.innerRef}
                            {...provided.draggableProps}
                            {...provided.dragHandleProps}
                            style={getItemStyle(
                              snapshot.isDragging,
                              provided.draggableProps.style
                            )}
                          > 
                          <input style={{height: "20px", width: "20px", zIndex: 10}} type="checkbox"
                          defaultChecked={item.isComplete}
                          onChange={(event) => {
                            var tempActionSteps = props.state;
                            tempActionSteps[index][1] = !tempActionSteps[index][1]
                            props.editToDoProperty("actionSteps", tempActionSteps )}}
                          />{item.content}
                          <div style={{width: "calc(90% - 50px)", display: "inline-block", position: "absolute", left: "20px", bottom: 0}} className="hidden-button">
                          
                          <button style={{cursor: "pointer", float: "right"}}  onClick={() => {
                            var tempActionSteps = removeByIndex(props.state, index);
                            props.editToDoProperty("actionSteps", tempActionSteps )}}>
                              <TrashIcon /></button>
                              <button style={{cursor: "pointer", float: "right"}}  onClick={() => {
                            var tempActionSteps = props.state;
                            var newAcionStepName = prompt("New Action Step Name")
                            tempActionSteps[index][0] = newAcionStepName
                            props.editToDoProperty("actionSteps", tempActionSteps )}}>
                              <EditIcon /></button>
                              </div>
                          </div>
                        )}
                      </Draggable>
                    )})}
                    </div>
                    {provided.placeholder}
                  </div>
                )}
                
              </Droppable></div>
  
              </div>
            )})}
          </DragDropContext>
        </div>
      </div>
    );
  }
  
class CalendarIcon extends React.Component {
    render() {
      return (<div style={{transform: "translateY(5px)", display: "inline-block"}}>
        <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 0 24 24" width="24px" fill="#E02424"><path d="M0 0h24v24H0z" fill="none"/><path d="M17 12h-5v5h5v-5zM16 1v2H8V1H6v2H5c-1.11 0-1.99.9-1.99 2L3 19c0 1.1.89 2 2 2h14c1.1 0 2-.9 2-2V5c0-1.1-.9-2-2-2h-1V1h-2zm3 18H5V8h14v11z"/></svg>
        </div>
      )
    }
}
  
  
class ScheduleDueDateButton extends React.Component {
    constructor(props) {
      super(props);
      this.state = {popUp: false, dueDate: (this.props.toDoItem.dueDate? this.props.toDoItem.dueDate : "Schedule")}
    }
    renderPopUp = () => {
      if (!this.state.popUp) {
        return null
      }
      return (
        <>
        <div style={{position: "fixed", height: "100vh", width: "100vw"}} onClick={this.changePopUp}></div>
        <div style={{width: "300px", position: "fixed", zIndex: 10}}>
          <Calendar onChange={(date) => this.scheduleCalendar(date)} className="react-calendar"/>
        </div>
        </>
      )
    }
    scheduleCalendar = (date) => {
      var stringDate = `${date.getMonth()+1}/${date.getDate()}`
      this.props.editToDoProperty("dueDate", stringDate)
      this.setState({dueDate: stringDate})
    }
    changePopUp = () => this.setState({popUp: !this.state.popUp})
    render() {
      return (
        <>
        <div style={{width: "100px", border: "2px solid #E02424", borderRadius: "5px",
        cursor: "pointer", padding: "2px", paddingTop: "0"}} onClick={this.changePopUp}>
                <CalendarIcon /> <p1>{this.state.dueDate}</p1>
              </div>
        {this.renderPopUp()}
        </>
      )
      
    }
}

class DropDownMenu extends React.Component {
  constructor(props) {
    super(props)
    this.state ={show: false, teamMembers: [], selectedUser: "none"}
    this.updateTeamMembers()
  }
  updateTeamMembers = async () => {
    var teamMembers = await  getTeamMembers()
    this.setState({teamMembers: teamMembers})
  }
  render() {
    
    return (<div style={{zIndex: 500}}>
      {(!this.state.show ? null : <div 
      onClick={() => this.setState({show: false})}
      style={{position: "fixed", top: 0, left: 0, height: "100vh", width: "100vw",zIndex: 1}}></div>)}
      <div style={{zIndex: 2, position: "fixed"}}>
      <div style={{backgroundColor: "#5E72E4", height: "30px", width: "110px", borderRadius: "7px",
      display: "flex", justifyContent: "center", alignItems: "center", color: "white", cursor: "pointer"
    }} onClick={() => this.setState({show: !this.state.show})}>
       {this.state.selectedUser}<ExpandDownwardIcon /></div>
      {(!this.state.show ? null :<div style={{zIndex: 2, position: "relative", backgroundColor: "white", borderRadius: "5px", border: "solid 1px lightgrey"}}>
        <div style={{marginBottom: "10px",marginTop: "10px",}}>
        {this.state.teamMembers.map(name => <div style={{display: "flex", justifyContent: "center", alignItems: "center",  height: "30px"}} className="dropdown-menu-item">{name}</div>)}
        
        </div>
      </div>
      )}
      </div>
    </div>)
  }
}

class DifficultySliders extends React.Component {
  constructor(props) {
    super(props)
    this.state = {difficultyValue: parseInt(this.props.toDoItem.difficulty)*10}
  }
  render() {
    return(
      <div style={{ position: "relative"}}>

            Difficulty ( /10)
            <div>
            <Slider
              axis="x"
              x={this.state.difficultyValue}
              onChange={({x}) => {
                this.props.editToDoProperty("difficulty",Math.floor(this.state.difficultyValue/10))
                this.setState({difficultyValue: x})}}
            /></div>
            <div>{Math.floor(this.state.difficultyValue/10)}</div>
            </div>
            
    )
  }
}


class DurationSliders extends React.Component {
  constructor(props) {
    super(props)
    this.state = {difficultyValue: parseInt(this.props.toDoItem.difficulty)*10, durationValue: parseInt(this.props.toDoItem.hours)*2}
  }
  render() {
    return(<>
      <div style={{position: "relative"}}>
            Hours ( /100)
            <div>
            <Slider
              axis="x"
              x={this.state.durationValue}
              onChange={({x}) => {
                this.props.editToDoProperty("hours",Math.floor(this.state.durationValue/2))
                this.setState({durationValue: x})}}
            /></div>
            </div>
            <div>
            <div>{Math.floor(this.state.durationValue/2)}</div>
            </div>
</>
    )
  }
}

class ExpandDownwardIcon extends React.Component {
  render() {
    return (
      <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 0 24 24" width="24px" fill="white"><path d="M0 0h24v24H0z" fill="none"/><path d="M16.59 8.59L12 13.17 7.41 8.59 6 10l6 6 6-6z"/></svg>
    )
  }
}


class TeamSelect extends React.Component {
  constructor(props) {
    super(props)
    this.state = {teamMembers: [], assignedMembers: this.props.toDoItem.assignedUser, key: false}
    console.log(this.props.toDoItem.assignedUser)
    this.updateTeamMembers()
  }
  updateTeamMembers = async ( ) => {
    var teamMembers  = await getTeamMembers()
    this.setState({teamMembers: teamMembers})
  }
  changeAssignedMember = async (member) => {
    var assignedMembers = this.state.assignedMembers;
    if (!assignedMembers.includes(member)){
    assignedMembers.push(member)}
    else {
      var index = assignedMembers.indexOf(member)
      assignedMembers.splice(index, 1)
    }
    this.props.editToDoProperty("assignedUser", assignedMembers)
    this.setState({assignedMembers: assignedMembers, key: !this.state.key})
    
  }
  render() {
    return(
      <div key={this.state.key}
      style={{borderRadius: "5px", backgroundColor: "#f8f8f8", overflow: "hidden"}}>
        {this.state.teamMembers.map((student, index) => <div style={{display: "inline-block", 
        backgroundColor: ((this.state.assignedMembers.includes(student))? "#e8e8e8": null),
        padding: "10px", borderLeft: ((index===0)? null :"2px solid lightgrey"),
        cursor: "pointer",
        zIndex: 15
        }}
        onClick={() => {this.changeAssignedMember(student)}}
        >{student}</div>)}
      </div>
    )
  }
}