var classActive = "empty";
const socket = new WebSocket('wss://cambio-socket.herokuapp.com/');
socket.addEventListener('message', function (event) {
    if (event.data === "end_class") {
        classActive = false
    }
    else if (event.data === "start_class") {
        classActive = true
    }
  });
export var isClassActive = async () => {
    if (classActive != "empty") {
        return classActive
    }

    var userData = await getUserInfo()
    await axios.get(`https://cambio-api.herokuapp.com/api/School_Class/${userData["class_name"]}/`, {headers: {
        'Authorization': `token ${localStorage.getItem("token")}`
        }
        }).then(res => {
            console.log(res.data)
            classActive = res.data.class_active
        }).catch(err => console.log(err))
    return classActive

}
function makeToDoList(lst, offset = 0) {
    return (lst.map((content, index) => {
        content = content.split("//>")
        return ({
        id: `${index} ${offset}`,
        content: content[0],
        assignedUser: JSON.parse(content[1]),
        difficulty: content[2],
        hours: content[3],
        dueDate: content[4],
        actionSteps: JSON.parse(content[5])
        //assignedUser: "John" //Doesnt work because of setState not updatig in ToDo
    })}
    ))
}
