import React from "react";
import { NotificationIcon } from "./icons";
export class NotificationBell extends React.Component {
    constructor(props) {
        super(props)
        this.state = {notificationOpen: false, notifications: [{title: "Join class", link: "/#/class", date: "April 1st"}]}
    }
    openNotifications = () => {
        this.setState({notificationOpen: true})
    }
    closeNotification = () => {
        this.setState({notificationOpen: false})
    }
    render() {
        //not ready for use yet
        return <></>
        return (<><button style={{cursor: "pointer", display: "inline-block"}} onClick={this.openNotifications}><NotificationIcon />
        {(this.state.notifications.length != 0) ? <div style={{position: "absolute", borderRadius: "1000px", backgroundColor: "orange", bottom: "30px", height: "19px", width: "19px"}}>{this.state.notifications.length}</div>: null}
        </button>
        {this.state.notificationOpen ? <NotificationWidget close={this.closeNotification} notifications={this.state.notifications}/> :null}
        </>)
    }
}

class NotificationWidget extends React.Component {
    render() {
        return (
            <><div className="popup-background-div" style={{position: "fixed",top: "0vh"}} onClick={this.props.close}></div>
            <div style={{height: "400px", width: "400px", backgroundColor: "white", position: "fixed", transform: "translateX(-370px)", float: "right", borderRadius: "20px", overflowX: "hidden", border: "1.5px solid lightgrey"}} className="scrollThing">
                <div style={{width: "100%", padding: "20px", backgroundColor: "#f8f8f8"}}> Notifications</div>
            {this.props.notifications.map((item) => <NotifcationItem item={item} />)}
            </div>
            </>
        )
    }
}

class NotifcationItem extends React.Component {
    render() {
        return <div style={{width: "100%", height: "50px", padding: "20px", backgroundColor: (!this.props.item.read ? "red": "lightgrey")}} onClick={
           () =>  {window.location.href = this.props.item.link}
        }>
            {this.props.item.title}
            <div>
                {this.props.item.date}
            </div>
            </div>
    }
}