import React from 'react';
import "../../style.css"
import { getSprintDates, updateSprintDates} from '../../backend.js';
import Calendar from 'react-calendar';
import './Calendar.scss';



export class SprintDates extends React.Component {
    constructor(props) {
      super(props);
      this.state= {popUp: false, firstSprint: "m/dd - m/dd", numSprints: 0, dates: []}
      this.refreshSprints()
      
    }
    removeSprintDate = (state, index) => {
      /*var newList = sprintDraggableToRegularList(removeByIndex(state, index))
      updateSprintDates(newList)
      this.setState({allDates: newList})   */                     
    }
    refreshSprints = async () =>{
        var currentSprints = await getSprintDates()
        if (currentSprints === null) {
          currentSprints = []
        }
        this.changeSoonestSprint(soonestSprint(currentSprints), currentSprints.length)
        this.setState({dates: currentSprints})
    }
  
    changeSoonestSprint = (sprint, num) => {
      var stringSprint = sprint[0] + " - " + sprint[1]
      this.setState({firstSprint: stringSprint, numSprints: num})
    }
    changePopUp = () => this.setState({popUp: !this.state.popUp})
    render() {
      return (
        <>
        {this.state.popUp ? <SprintDatesPopUp changePopUp={this.changePopUp} changeSoonestSprint={this.changeSoonestSprint}
        allDates={this.state.dates}
        removeSprintDate={this.removeSprintDate}
        /> : null}
        <div style={{cursor: "pointer"}} onClick={this.changePopUp}>
          
        <svg xmlns="http://www.w3.org/2000/svg" width="400" height="auto" viewBox="0 0 683 84">
    <rect id="Rectangle_1467" data-name="Rectangle 1467" width="683" height="84" rx="30" fill="#f8f8f8"/>
    <rect id="Rectangle_1495" data-name="Rectangle 1495" width="72" height="55" rx="18" transform="translate(141 15)" fill="#fff"/>
    <text id="Sprint_" data-name="Sprint#" transform="translate(27 56)" stroke="#707070" stroke-width="1" font-size="30" ><tspan x="0" y="0">Sprint</tspan></text>
    <rect id="Rectangle_1496" data-name="Rectangle 1496" width="185" height="55" rx="18" transform="translate(448 15)" fill="#fff"/>
    <text id="Dates_" data-name="Dates    " transform="translate(355 54)" stroke="#707070" stroke-width="1" font-size="30" ><tspan x="0" y="0" xmlSpace="preserve">Dates    </tspan></text>
    <text id="m_d_-_m_d_" data-name="m/d - m/d    " transform="translate(465 51)" stroke="#707070" stroke-width="1" font-size="27" ><tspan x="0" y="0" xmlSpace="preserve">{this.state.firstSprint}</tspan></text>
    <text id="_1" data-name="1" transform="translate(153 54)" stroke="#707070" stroke-width="1" font-size="27" ><tspan x="0" y="0">#{this.state.numSprints}</tspan></text>
  </svg>
  </div>
  </>
      )
    }
  }
  
  class SprintDatesPopUp extends React.Component {
    constructor(props) {
      super(props)
      this.state = {dates: ["m/dd", "m/dd", ""], error: [null, null], allDates: this.props.allDates}
    }
    inputChangeDates = (date, index) => {
      var tempDate;
      try {
        if (index <2){
          
        var tempDateList = date.split("/")
        parseInt(tempDateList[0])
        parseInt(tempDateList[1])
        }
        tempDate = this.state.dates;
      tempDate[index] = date
      }
      catch {
       tempDate = this.state.dates
        return;
      }
      this.setState({dates: tempDate})
    }
    addAllDates = (e = {keyCode: 13}) => {
      if (e.keyCode !== 13) {
        return
      }
      //idk why this dont work
      if (this.state.dates[0] === "m/dd" || this.state.dates[1] === "m/dd") {
        return;
      }
      var tempAllDates = this.state.allDates;
      tempAllDates.push(this.state.dates)
      //this.props.changeSoonestSprint(soonestSprint(this.state.allDates), this.state.allDates.length)
      this.props.changeSoonestSprint(this.state.dates, this.state.allDates.length)
      this.setState({dates: ["m/dd", "m/dd", ""]})
    }
    removeSprintDate = (lst, index) => {
      
      
      this.props.removeSprintDate(lst,index)
      
      //this.setState({allDates: sprintDraggableToRegularList(removeByIndex(lst, index))})
    }
    render() {
      updateSprintDates(this.state.allDates)
      return (<>
      <div className="popup-background-div" >
      <div style={{height: "430px", width:"700px", backgroundColor: "white", zIndex: "20", position: "relative", padding: "15px", borderRadius: "20px"}}>
        <Calendar selectRange={true} 
        onChange={(value) => this.setState({dates: makeDisplayDate(value, this.state.dates[2])})} 
        value={makeDateObject(this.state.dates)} className="react-calendar"/>
        <div style={{position: "absolute", right: "30px", top: "30px"}}>
          <p1 style={{fontWeight: "bold", fontSize: "20px"}}>Add Sprint Date</p1>
          <div>
            Start: 
            <input style={{width: "80px", display: "inline-block", marginRight: "30px", height: "35px"}} 
            placeholder={this.state.dates[0]} 
            onChange={(text) => this.inputChangeDates(text.target.value, 0)} onKeyDown={(e) => this.addAllDates(e)}/>
          End: 
          <input  style={{width: "80px", height: "35px"}} 
          placeholder={this.state.dates[1]} 
          onChange={(text) => this.inputChangeDates(text.target.value, 1)} onKeyDown={(e) => this.addAllDates(e)}/>
          </div>
         {/* <input style={{display:"block"}} placeholder={"Description"} 
          onChange={(text) => this.inputChangeDates(text.target.value, 2)} onKeyDown={(e) => this.addAllDates(e)}/>*/}
          <button style={{backgroundColor: "#8B4389", color: "white", borderRadius: "5px", cursor: "pointer"}} onClick={() => this.addAllDates({keyCode: 13})}>Add Sprint</button>
      <div style={{display: "block"}} key={JSON.stringify(this.state.allDates)}> 
            
            {/*<SprintDatesDraggable state={this.state.allDates} removeSprintDate={this.removeSprintDate}/> */}
            <SprintDatesInfo sprintDate={this.state.allDates[this.state.allDates.length-1]} removeLastDate={
              () => {
                var tempAllDates = this.state.allDates;
                tempAllDates.pop()
                //window.location.reload()
                this.setState({allDates: tempAllDates})
              }
            }/>
          </div>
          </div>
      </div>
      <div className="popup-clickable-background-div" onClick={this.props.changePopUp}>
      </div>
      
      </div>
      </>)
    }
  }
  function makeDateObject(dateList) {
    if ((dateList[0] === "m/dd") || (dateList[1]  === "m/dd")) {
      return null
    }
    return dateList.map(date => {
      var tempDateList = date.split("/")
      return new Date(parseInt(
        parseInt(JSON.stringify(new Date()).substr(1,4))
      ),parseInt(tempDateList[0])-1, parseInt(tempDateList[1]))
    })
  }
  
  function makeDisplayDate(dateList, description) {
    var tempDateList =  dateList.map(date => {
      return `${date.getMonth()+1}/${date.getDate()}`
    })
    tempDateList.push(description)
    return tempDateList;
  }
  
  function soonestSprint(datesList) {
    if (datesList.length === 0) {
      return ["m/dd", "m/dd"]
    }
    
    var dateIndex = 0;
    var dates = 1000000000
    datesList.forEach((date, index) => {
      var dateSplit = date[1].split("/")
      var integerDate = parseInt(dateSplit[0])*30 + parseInt(dateSplit[1])
      if (integerDate < dates) {
        dateIndex = index
        dates = integerDate
      }
    })
    return datesList[dateIndex]
  }
  
  class SprintDatesInfo extends React.Component {
    render() {
      var sprintDate = this.props.sprintDate
      if (!sprintDate) {
        sprintDate = ["m/dd", "m/dd", ""]
        return <></>;
      }
      return (<div style={{backgroundColor: "#f8f8f8", marginTop: "20px", padding: "20px", borderRadius: "20px"}}>
        Due: {`${sprintDate[0]} - ${sprintDate[1]}`}
        {/*<div>
          Description: {sprintDate[2]}
        </div>
        <div onClick={this.props.removeLastDate} >Delete</div>
        <div >Complete</div>*/}
      </div>)
    }
  }